.content {
  font-size: 18px;
  font-weight: 500;
  flex: 0 1 80%;
  text-align: left;
  color: white;
}


.profile {

}
.img img {
  display: block;
  max-width: 100%;
  min-width: 100%;
  height: 400px;
}

.info {
  margin-bottom: 20px;
  width: 70%;
  padding: 0px 0px 20px 0px;
  position: relative;
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;

}
.description {
  font-size: 24px;
  margin-bottom: 20px;
  
}


.description:first-child {
  display: flex;
  align-items: center;
  
}
.about  {
  display: block;
  margin: 15px 0;

}
.list {
  color: blueviolet;
  list-style: none;
  margin: 5px 0;
}
.list a {
  color: white;
}

.list a:hover {
  
  color: blueviolet;
}
.uploadFile { 
  flex: 1 1 100%;
  display: block;
  max-width: 200px;
  min-width: 200px;
  border-radius: 30px;
  padding: 10px 15px;
}
.fullName {
  flex: 1 1 100%;
  margin-bottom: 20px;
  display: block;
}
.profileInfo {
  flex-wrap: wrap;
  margin-left: 20px;
}
.button {
  padding: 10px 15px;
  border: 1px solid blueviolet;
  color: white;
  background-color: rgba(25, 43, 59, 0);
  font-size: 14px;
  font-weight: bold;
}
.about b {
}
.placeholder {
  border: 2px solid blueviolet;
  margin-top: 5px;
  padding: 5px;
  resize: none;
  background-color: rgba(240, 248, 255, 0);
  color: white;
}

.social {
  margin-bottom: 10px;
}

.title {
  margin-bottom: 10px;
  display: block;
}

 .descr > .description  {
  display: block;
}